.ItemImg {
    display: flex;
    margin-top: 2vh;
    margin-bottom: 2vh;
    border: 2px solid transparent;
}

.ItemImg.even {

    flex-direction: row-reverse;
}

.ItemImg .link-filter {

    border: 2px solid transparent;
}

.status-control {

    height: 100%;
}
.ItemImg .link-filter:focus {

    border-color: rgba( 42,34,71,.8 );
}

.fig-picsy {

    position: absolute;
    background: rgba( 0,0,0,.85 );
    width: 40px;
    height: 35px;
    transform: scale( 0 );
    transition: .25s linear all;
}

figure:hover .fig-picsy {

    transform: scale( 1 );
}

.fig-picsy button {

    position: relative;
    color: #fff;
    left: 8px;
    opacity: .65;
    font-size: 22px;
    transition: .1s ease-in-out all;
}

.fig-picsy button:hover {

    transform: rotate( 90deg) scale(1.3) ;
    opacity: 1;
}
 