.ListImg {
    display: flex;
    flex-wrap: wrap;
    width: 80%;
    min-width: 300px;
    justify-content: space-between;
    margin: 2vh auto;
}


@media screen and ( max-width: 850px ) {
 
    .ListImg {

        flex-direction: column;
        align-items: center;
        width: 100%;
    }
}